* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

pre {
  background-color: #f7f7f7;
  padding: 10px;
}

table {
  margin-bottom: 20px;
}

th, td {
  border: 2px solid #545454;
  padding: 5px;
}

blockquote {
  border-left: 3px solid #4ec456;
  padding-left: 10px;
  margin-left: 25px;
  margin-right: 40px;
}

#root {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: red;
  max-height: 100vh;
  overflow: hidden;
  position: relative;
}
@media (min-width: 992px) {
  .wrapper {
    flex-direction: row;
  }
}

.screen {
  flex-grow: 0;
  flex-shrink: 0;
  width: 100%;
  max-width: 100vw;
  height: 50vh;
  position: absolute;
  left: 0;
  z-index: 1;
}
@media (min-width: 992px) {
  .screen {
    width: 50vw;
    height: 100%;
    top: 0;
  }
}
.screen_type_code {
  background-color: #E77F7F;
  color: white;
  top: 0;
}
@media (min-width: 992px) {
  .screen_type_code {
    left: 0;
  }
}
.screen_type_code .screen__nav-element {
  color: black;
  background-color: rgba(255, 255, 255, 0.4);
}
.screen_type_code .screen__nav-element:hover {
  background-color: rgba(255, 255, 255, 0.6);
}
.screen_type_viewer {
  background-color: #fff;
  top: 50vh;
}
@media (min-width: 992px) {
  .screen_type_viewer {
    top: 0;
    left: 50vw;
  }
}
.screen_type_viewer .screen__nav-element {
  background-color: rgba(233, 233, 233, 0.44);
}
.screen_type_viewer .screen__nav-element:hover {
  background-color: rgba(233, 233, 233, 0.8);
}
.screen__nav {
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  opacity: 1;
  transition: 0.45s;
}
@media (min-width: 992px) {
  .screen__nav {
    opacity: 0.3;
  }
}
.screen__nav-element {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 55px;
  height: 55px;
  cursor: pointer;
  transition: 0.45s;
}
.screen__nav-element-icon {
  font-size: 25px;
}
.screen__code-area {
  background-color: transparent;
  width: 100%;
  height: 100%;
  padding: 74px 30px;
  font-size: 18px;
  color: white;
  resize: none;
}
.screen__code-area:focus {
  outline: none;
}
.screen__preview-area {
  padding: 74px 30px;
  overflow-y: auto;
  height: 100%;
}
.screen__preview-area img, .screen__preview-area svg, .screen__preview-area iframe {
  max-width: 100%;
}
.screen:hover .screen__nav {
  opacity: 1;
}
.screen_full-screen {
  height: 100vh;
  top: 0;
  z-index: 2;
}
@media (min-width: 992px) {
  .screen_full-screen {
    width: 100vw;
    left: 0;
  }
}

::-webkit-scrollbar {
  width: 5px;
  border: none;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: gainsboro;
  cursor: pointer;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

::-webkit-scrollbar-corner {
  display: none;
}